@import url("https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap");



* {
  margin: 0;
  font-family: "Quicksand";
  color: #848282;
}

/* NavBar*/
.navbar {
  padding: 20px;
  display: flex;
  align-items: center;
  max-width: 600px;
  margin: 0 auto;
  border-bottom: 1px solid #848282;
}
.navbar h1 {
  color: #f1356d;
}
.navbar .links {
  margin-left: auto;
}
.navbar a {
  margin-left: 16px;
  text-decoration: none;
  padding: 6px;
}
.navbar a:hover {
  color: #f1356d;
  font-weight: bold;
}

/* Home Page Content */
.content {
  max-width: 600px;
  margin: 60px auto;
  padding: 20px;
}

.blog-preview {
  margin: 30px 0;
  padding: 10px;
  border-bottom: 1px solid #848282;
}

.blog-preview:hover {
  box-shadow: 1px 3px 5px;
  cursor: pointer;
}

.blog-preview p {
  color: #f1356d;
  font-size: 15px;
  margin-bottom: 8px;
}



/* create new blog form */
.create {
  max-width: 400px;
  margin: 0 auto;
  text-align: center;
}
.create label {
  text-align: left;
  display: block;
}
.create h2 {
  font-size: 20px;
  color: #f1356d;
  margin-bottom: 30px;
}
.create input, .create textarea, .create select {
  width: 100%;
  padding: 6px 10px;
  margin: 10px 0;
  border: 1px solid #ddd;
  box-sizing: border-box;
  display: block;
}
.create button {
  background: #f1356d;
  color: #fff;
  border: 0;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
}


article button{
  background: #f1356d;
  color: #fff;
  border: 0;
  margin-top: 10px;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
}